.wizardContainer {
  padding: 1.6rem;
  text-align: center;
}

.title1 {
  font-size: 2em;
}

.navList {
  counter-reset: section;
  list-style: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  max-width: 50em;
}

.navItem {
  flex: 1;
  margin: 0 0.3em 0.3em;
}

.navSection {
  composes: navItem;
  width: 100%;
  flex-basis: 100%;
}
@media (min-width: 52em) {
  .navSection {
    flex-basis: initial;
    width: initial;
  }
}

.styledButton {
  color: inherit;
  background-color: transparent;
  border: 1px solid currentColor;
  padding: 0.35em 1.5em;
  border-radius: 0.9em;
  cursor: pointer;
  font: inherit;
  display: block;
  text-transform: capitalize;
  user-select: none;
  white-space: nowrap;
}
.styledButton.active, .styledButton.prev {
  color: #3abdd9;
}
.styledButton.active {
  cursor: default;
  background-color: #e2e2e2;
}
.styledButton:not(.active, .buttonSubmit, .navSectionButton):hover {
  background-color: #e2e2e2;
}

.navItemButton {
  width: 100%;
}

.navSectionButton {
  composes: styledButton;
  counter-increment: section;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-weight: bold;
  padding-left: 0.5em;
  width: 100%;
  text-align: left;
}
.navSectionButton:before {
  content: counter(section);
  font-size: 0.8em;
  height: 1.4em;
  width: 1.4em;
  background-color: #0d131e;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  margin-right: 0.5em;
}
.navSectionButton.active, .navSectionButton.prev {
  background-color: #3abdd9;
  color: #fbfbfb;
}
.navSectionButton:not(.active):hover {
  background-color: #24a1bc;
}

.wizardMain {
  padding: 2em 3vw;
  text-align: initial;
  max-width: 50em;
  border: 1px solid #ccc;
  margin: 1.5em auto;
  border-radius: 1em;
}

.question {
  margin-top: 0;
  font-size: 1.5em;
}

.formGroup {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.radioLabel {
  flex: 1;
  margin: 0 0.3em 0.3em;
}
.radioLabel input[type=radio] {
  display: none;
}
.radioLabel span {
  display: block;
  font-size: 1.2em;
  padding: 0 1.2em;
  line-height: 2.1;
  border: 1px solid #afafaf;
  border-radius: 0.5em;
  cursor: pointer;
  text-align: center;
  user-select: none;
}
.radioLabel input[type=radio]:checked + span {
  border-color: #3abdd9;
}

.controlInput {
  display: block;
  width: 100%;
  font-size: 1.2em;
  line-height: 2;
  padding-left: 1.2em;
  border: 1px solid #afafaf;
  border-radius: 0.5em;
  margin-bottom: 0.3em;
}
.controlInput:hover, .controlInput:focus {
  outline-width: 0.1rem;
  outline-color: #3abdd9;
}

.wizardFooter {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.wizardFooter span {
  margin-right: 0.6em;
  display: flex;
  align-items: center;
  white-space: nowrap;
}
.wizardFooter b {
  margin-left: 0.3em;
}
.wizardFooter svg {
  margin-right: 0.3em;
}

.footerButton {
  font-size: 1.4em;
  border-radius: 0.2em;
  padding: 0.4em 2em;
}

.buttonNext {
  composes: styledButton;
}

.buttonSubmit {
  composes: styledButton;
  composes: footerButton;
  background-color: #3abdd9;
  color: #fbfbfb;
}
.buttonSubmit:hover {
  background-color: #24a1bc;
}